<template>
  <v-main>
    <div class="mb-4 mx-4 mt-n6" style="font-size: 120%">
      加入率 {{ this.NumOfObjective }} / {{ this.CharaLength }}
    </div>

    <v-layout row class="mx-3" style="flex-wrap: wrap" id="main">
      <v-card
        v-for="chara in charaList"
        v-bind:key="chara['m_CharaID']"
        class="ma-0 pa-0 d-flex flex-wrap"
        style="flex-wrap: wrap"
      >
        <v-card
          style="line-height: 0px; flex-wrap: wrap"
          :width="elm_length"
          :height="elm_length"
          aspect-ratio="1"
          @click="switchObjData(chara['m_CharaID'])"
          class="checkWidth"
        >
          <img
            crossOrigin="anonymous"
            :id="'Img' + chara['m_CharaID']"
            :src="'https://y52en.github.io/kirafan-checker/mergedcharaicon/charaicon_' + chara['m_CharaID'] + '.png'"
            aspect-ratio="1"
            width="100%"
            decoding="async"
            style="position: absolute; z-index: 2"
          />
          <img
            :id="'Img' + chara['m_CharaID']"
            src="../assets/loading.gif"
            aspect-ratio="1"
            width="100%"
            decoding="async"
            style="position: absolute"
          />
          <img
            :id="'Obj' + chara['m_CharaID']"
            crossOrigin="anonymous"
            src="../assets/Owned.png"
            aspect-ratio="1"
            width="100%"
            :style="switchStyle(chara['m_CharaID'])"
          />
        </v-card>
      </v-card>
    </v-layout>
    <span style="display: none">
      <canvas id="canvas" width="1000"> </canvas>
    </span>
    <v-snackbar color="info" v-model="isMakingImage" timeout="10000">
      画像生成中…
    </v-snackbar>

    <v-snackbar color="error" v-model="Err_MakingImage">
      画像の生成に失敗しました。画像がロードされていない可能性があります。
    </v-snackbar>
  </v-main>
</template>

<script>
export default {
  name: "home",

  data: () => ({
    isLoaded: false,
    isMakingImage: false,
    Err_MakingImage: false,

    elm_length: "50px",

    force_update: 0,
  }),

  computed: {
    NumOfObjective() {
      return Object.keys(this.getObjChara()).filter(
        (x) => this.getObjChara()[x]
      ).length;
    },
    CharaLength() {
      return this.charaList.length;
    },
  },

  methods: {
    cardLength() {
      let elm = document.querySelector("#main");

      if (elm === null) {
        return "50px";
      } else {
        let ColumnLength = (() => {
          if (this.getCheckerSettings("useColumnLength")) {
            return this.getCheckerSettings("ColumnLength") || 8;
          } else {
            return (
              Math.floor(
                elm.clientWidth /
                  (this.getCheckerSettings("standardLength") || 50)
              ) || 1
            );
          }
        })();
        return `${elm.clientWidth / ColumnLength - 1}px`;
      }
    },
    // async getImage(id) {
    // //https://card-assets.kirafan.cn
    // const ImageURL = `../mergedcharaicon/charaicon_${id}.png`;
    // if (this.storage.isSupported) {
    // //   let blob = await this.storage.getItem(id);
    // //   if (blob === undefined) {
    // //     let download_img = await fetch(ImageURL);
    // //     blob = await download_img.blob();
    // //     this.storage.setItem(id, blob);
    // //   }
    // //   return await URL.createObjectURL(blob);
    // // } else {
    //   return ImageURL;
    // }
    // },

    async outputImage() {
      try {
        const NumOfObjective = () =>
          Object.keys(this.getObjChara()).filter((x) => this.getObjChara()[x])
            .length;

        const CharaLength = () => this.charaList.length;

        this.isMakingImage = true;
        const canvas = document.getElementById("canvas");
        const ctx = canvas.getContext("2d");
        const header = 60;
        canvas.height = Math.ceil(this.charaList.length / 10) * 100 + header;

        ctx.fillStyle = "rgb(255,255,255)";
        ctx.fillRect(0, 0, canvas.width, canvas.height);

        ctx.font = "48px Arial";
        ctx.fillStyle = "rgb(0,0,0)";

        ctx.fillText(`加入率：${NumOfObjective()} / ${CharaLength()}`, 5, 50);

        for (let i = 0; i < this.charaList.length; i++) {
          let charaId = this.charaList[i]["m_CharaID"];
          let image_elm = document.getElementById("Img" + charaId);
          const draw = (elm) =>
            ctx.drawImage(
              elm,
              100 * (i % 10),
              100 * Math.floor(i / 10) + header,
              100,
              100
            );

          draw(image_elm);
          if (this.getObjChara(charaId)) {
            let obj_elm = document.getElementById("Obj" + charaId);
            draw(obj_elm);
          }
        }

        // let vm = this;
        canvas.toBlob(callback);
      } catch (error) {
        this.Err_MakingImage = true;
        this.isMakingImage = false;
        console.error(error);
      }

      let vm = this;

      async function callback(x) {
        downloadFromUrlAutomatically(
          await URL.createObjectURL(x),
          `${dateFunction()}.png`
        );
        vm.isMakingImage = false;
      }

      function dateFunction() {
        //日付オブジェクトを作成する
        var dd = new Date();
        //「年」を取得する
        var YYYY = dd.getFullYear();
        //「月」を取得する
        var MM = fixed(dd.getMonth() + 1, 2);
        //「日」を取得する
        var DD = fixed(dd.getDate(), 2);
        //段落タグ("date011")の中身を置き換える
        return "" + YYYY + MM + DD;

        function fixed(num, toDegit) {
          let stredNum = "" + num;
          for (let i = 0; i < toDegit - stredNum.length; i++) {
            stredNum = "0" + stredNum;
          }
          return stredNum;
          //   return "0" + num
          // }else{
          //   return "" + num
          // }
        }
      }

      function downloadFromUrlAutomatically(url, fileName) {
        // var xhr = new XMLHttpRequest();
        // xhr.open("GET", url, true);
        // xhr.responseType = "blob";
        // xhr.onload = function () {
        //   if (this.status == 200) {
        // var urlUtil = window.URL || window.webkitURL;
        // var imgUrl = urlUtil.createObjectURL(this.response);
        var link = document.createElement("a");
        link.href = url;
        link.download = fileName;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        //   }
        // };
        // xhr.send();
      }
    },

    switchStyle(id) {
      if (this.getObjChara(id)) {
        return "position:relative;z-index: 5;";
      } else {
        return "display:none;";
      }
    },

    switchObjData(id) {
      let objChara = !this.getObjChara(id);
      this.setObjChara(id, objChara);
      if (this.storage.isSupported)
        this.storage.setItem("ObjectiveChara", this.getObjChara());
    },
    UpdateSize() {
      this.elm_length = this.cardLength();
      this.$forceUpdate();
    },
  },

  props: {
    charaList: {
      type: Array,
    },
    storage: {
      type: Object,
    },
    setObjChara: {
      type: Function,
    },
    getObjChara: {
      type: Function,
    },
    getCheckerSettings: {
      type: Function,
    },
  },

  mounted() {
    this.$emit("dl-image", this.outputImage);
    this.$emit("update-size", this.UpdateSize);

    let vm = this;

    window.onorientationchange = () => {
      this.UpdateSize();
    };
    window.onresize = () => {
      this.UpdateSize();
    };

    // document.getElementById("root").style["width"] = window.innerWidth
    vm.elm_length = vm.cardLength();
    vm.$forceUpdate();
    // setTimeout(()=>{
    // },2000)
    // console.log(vm.cardLength())
    //let id =
    // setInterval(()=>{
    //   if(document.getElementsByClassName("checkWidth")[0].style["width"] !== vm.cardLength()){
    //     vm.$forceUpdate()
    // console.log([0].style["width"], vm.cardLength())

    // console.log(document.getElementById("main").clientWidth);
    // console.log(vm.elm_length);

    // let elm = document.getElementsByClassName("checkWidth")
    // for(let i = 0;i<elm.length;i++){
    //   document.getElementsByClassName("checkWidth")[i].style["width"] = vm.elm_length
    // }

    //   }
    // },250)
  },
};
</script>
