<template>
  <v-app>
    <v-app-bar app color="primary" dark height="40px" style="z-index: 10">
      <div class="d-flex align-center">
        <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="./assets/icon.png"
          transition="scale-transition"
          width="40"
        />

        <span class="title">所持チェッカー</span>
      </div>

      <v-spacer></v-spacer>

      <template>
        <div class="text-center">
          <v-dialog v-model="settings" width="500">
            <template v-slot:activator="{ on, settings }">
              <v-btn v-bind="settings" v-on="on" target="_blank" icon>
                <v-icon>mdi-view-grid</v-icon>
              </v-btn>
            </template>

            <v-card class="pb-4">
              <v-card-title class="headline lighten-2">
                キャラアイコン設定
              </v-card-title>

              <v-divider></v-divider>

              <span class="mx-3"> 基本サイズ </span>
              <v-text-field
                label="サイズ(px)"
                placeholder="50"
                outlined
                class="mx-3"
                type="number"
                :value="checkerSettings.standardLength"
                @input="sizeInput"
                :disabled="checkerSettings.useColumnLength"
              ></v-text-field>

              <v-divider></v-divider>

              <v-checkbox
                v-model="checkerSettings.useColumnLength"
                label="横の表示アイコン数を固定する"
                :value="checkerSettings.useColumnLength"
                @input="useColumnLengthInput"
                class="mx-3"
              />

              <v-text-field
                label="アイコン数"
                placeholder="8"
                outlined
                class="mx-3"
                type="number"
                :value="checkerSettings.ColumnLength"
                @input="ColumnLengthInput"
                :disabled="!!!checkerSettings.useColumnLength"
              ></v-text-field>
            </v-card>
          </v-dialog>
        </div>
      </template>

      <template>
        <div class="text-center">
          <v-dialog v-model="dialog" width="500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" target="_blank" icon>
                <v-icon>mdi-text-shadow</v-icon>
              </v-btn>
            </template>

            <v-card class="pb-4">
              <v-card-title class="headline lighten-2">
                インポート/エキスポート
              </v-card-title>

              <v-divider></v-divider>

              <v-text-field
                v-model="input_import"
                label="キャラ所持データのインポート"
                class="mx-2"
              ></v-text-field>

              <v-btn
                @click="importData"
                right
                elevation="2"
                style="margin: 0 0 0 auto; right: 15px"
                class="mt-n2 mb-3 d-flex"
              >
                インポート
              </v-btn>

              <v-divider></v-divider>

              <v-card-actions>
                <v-row justify="center" align="center">
                  <!-- <v-col>  -->
                  <v-btn
                    text
                    outlined
                    v-clipboard:copy="copyStr"
                    v-clipboard:success="
                      () => {
                        this.copied = true;
                      }
                    "
                    class="mt-3 d-flex text-center"
                  >
                    キャラ所持データをクリップボードにコピー
                  </v-btn>
                </v-row>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
      </template>

      <!-- <v-btn target="_blank" text icon @click="dlImage">
        <v-icon>mdi-image-move</v-icon>
        <span class="ml-2">画像化</span>
      </v-btn> -->
    </v-app-bar>

    <v-main>
      <div v-if="isDownloading" class="text-center">
        <v-progress-circular
          rotate="-90"
          :value="progressCircular"
          color="primary"
          class="ma-3"
        ></v-progress-circular>
        <br />{{ progressStr }} <br />
        {{ downloadStatus.str }}
      </div>

      <Main
        v-if="isLoaded"
        :chara-list="charaList"
        :storage="storage"
        :img-url-list="initedImgList"
        :set-obj-chara="setObjChara"
        :get-obj-chara="getObjChara"
        :get-checker-settings="getCheckerSettings"
        @dl-image="dlImage = $event"
        @update-size="UpdateSize = $event"
        class="mb-10"
      />
    </v-main>

    <v-dialog v-model="Err_noSupportIndexedDB">
      <v-card class="py-5" align="center">
        IndexedDBを使用できないためブラウザにデータを保存できません。 <br />
        プライベートモードであるかブラウザが古い可能性があります。
      </v-card>
    </v-dialog>

    <!-- <v-dialog v-model="firstAccess">
      <v-card class="py-5" align="center">
        アクセスありがとうございます。 <br />
        画像の出力は右上の<v-icon>mdi-image-move</v-icon>で行うことができますのでご利用ください。<br />
        (このメッセージは次回以降表示されません)
      </v-card>
    </v-dialog> -->

    <v-snackbar color="info" v-model="isNotLoaded">
      ロード中はダウンロードできません
    </v-snackbar>

    <v-snackbar v-model="copied"> コピーしました </v-snackbar>

    <v-snackbar v-model="imported"> インポートしました </v-snackbar>

    <v-snackbar v-model="Err_invaild_input"> 入力に誤りがあります </v-snackbar>
  </v-app>
</template>

<style>
/* input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
  -moz-appearance: textfield;
} */
</style>

<script>
import Main from "./components/main.vue";
import IndexedDB from "./plugins/IndexedDB_module";

export default {
  name: "App",

  components: {
    Main,
  },

  // computed:{

  // },

  data: () => ({
    isPageJumping: false,

    dialog: false,
    settings: false,
    copied: false,
    imported: false,
    input_import: "",

    firstAccess: false,

    Err_noSupportIndexedDB: false,
    Err_invaild_input: false,

    isLoaded: false,
    isDownloading: false,
    downloadStatus: {
      downloaded: 0,
      max: 0,
      str: "",
    },
    isNotLoaded: false,

    charaList: [],
    initedImgList: {},

    storage: undefined,

    ObjectiveChara: {},
    checkerSettings: {},

    dlImage: () => {
      console.info("loading...");
    },
    UpdateSize: () => {},
  }),

  computed: {
    progressCircular() {
      const dl_status = this.downloadStatus;
      return (dl_status.downloaded / dl_status.max) * 100;
    },
    progressStr() {
      const dl_status = this.downloadStatus;
      return `${dl_status.downloaded} / ${dl_status.max}`;
    },
    copyStr() {
      return JSON.stringify(this.ObjectiveChara);
    },
  },

  methods: {
    sizeInput(input) {
      this.setCheckerSettings("standardLength", Number(input));
      this.UpdateSize();
    },
    useColumnLengthInput(input) {
      this.setCheckerSettings("useColumnLength", input);
      this.UpdateSize();
    },
    ColumnLengthInput(input) {
      this.setCheckerSettings("ColumnLength", Number(input));
      this.UpdateSize();
    },

    NowIsLoaded() {
      this.isNotLoaded = true;
    },

    setProgress(
      downloaded = ++this.downloadStatus.downloaded,
      max = this.downloadStatus.max,
      str = this.downloadStatus.str
    ) {
      const dl_status = this.downloadStatus;

      dl_status.downloaded = downloaded;
      dl_status.max = max;
      dl_status.str = str;
    },

    getXHRresponce(...Input) {
      return new Promise((resolve) => {
        let fileURL = Input[0];
        let callback = Input[1] || undefined;
        let fileType = Input[2] || "json";

        let xmlHttpRequest = new XMLHttpRequest();
        xmlHttpRequest.onreadystatechange = function () {
          if (this.readyState === 4 && this.status === 200) {
            if (this.response) {
              if (callback) callback();
              resolve(this.response);
            }
          }
        };

        xmlHttpRequest.open("GET", fileURL, true);
        // @ts-ignore
        xmlHttpRequest.responseType = fileType;
        xmlHttpRequest.send(null);
      });
    },

    setObjChara(propertyName, value) {
      this.$set(this.ObjectiveChara, String(propertyName), value);
      if (this.storage.isSupported)
        this.storage.setItem("ObjectiveChara", this.ObjectiveChara);
    },
    getObjChara() {
      if (arguments[0]) {
        return this.ObjectiveChara["" + arguments[0]];
      } else {
        // console.log(this.ObjectiveChara);
        return this.ObjectiveChara;
      }
    },

    setCheckerSettings(propertyName, value) {
      this.$set(this.checkerSettings, String(propertyName), value);
      this.storage.setItem("checkerSettings", this.checkerSettings);
    },
    getCheckerSettings() {
      if (arguments[0]) {
        return this.checkerSettings["" + arguments[0]];
      } else {
        // console.log(this.ObjectiveChara);
        return this.checkerSettings;
      }
    },

    importData() {
      try {
        const data = JSON.parse(this.input_import);
        if (Array.isArray(data)) throw new Error("err");
        this.ObjectiveChara = data;
        this.imported = true;
        if (this.storage.isSupported)
          this.storage.setItem("ObjectiveChara", this.ObjectiveChara);
      } catch (err) {
        this.Err_invaild_input = true;
      }
    },
  },

  async mounted() {
    this.dlImage = this.NowIsLoaded;
    this.storage = new IndexedDB("checker-friendship");
    await this.storage.init().catch(() => {
      this.Err_noSupportIndexedDB = true;
    });

    if (this.storage.isSupported) {
      //eslint-disable-next-line
      if (!!!(await this.storage.getItem("visited"))) {
        this.firstAccess = true;
        this.storage.setItem("visited", true);
      }
    }

    this.setProgress(0, 3, "DBダウンロード中…");
    this.isDownloading = true;

    const dbDL = (str) =>
      this.getXHRresponce(
        `https://database.kirafan.cn/database/${str}.json`,
        this.setProgress
      );

    let dbList = ["CharacterList", "TitleList", "NamedList"].map((x) =>
      dbDL(x)
    );

    await Promise.all(dbList).then((value) => {
      this.charaList = value[0];
      this.titleList = value[1];
      this.namedList = value[2];
    });

    // console.log(this.namedList);
    // console.log(this.charaList);

    this.charaList = 
    //this.charaList
      this.namedList
      // .filter((chara) => chara["m_Rare"] === 2)
      // .filter((chara) => /0$/.test(chara["m_CharaID"]))

      .map((chara)=>{
        // console.log(chara);
        const charaId = chara.m_NamedType;
        const charaCard_place = this.charaList.findIndex((card)=>{
          // console.log(card.m_NamedType , charaId);
          return (card.m_NamedType === charaId && /1$/.test(card["m_CharaID"]))
        })
        // console.log(charaCard_place);
        chara.m_CharaID = this.charaList[charaCard_place].m_CharaID
        return chara
      })

      .map((chara) => {
        chara["TitleID"] = (() => {
          let tmp = this.namedList.find(
            (namedList) => chara["m_NamedType"] === namedList["m_NamedType"]
          );
          return tmp["m_TitleType"];
        })();
        return chara;
      })

      .map((chara) => {
        chara["sort_TitleID"] = (() => {
          let tmp = this.titleList.find(
            (titleList) => chara["TitleID"] === titleList["m_TitleType"]
          );
          return tmp["m_Order"];
        })();
        return chara;
      })
      .sort((a, b) => a["m_NamedType"] - b["m_NamedType"])

      .sort((a, b) => a["m_CharaID"] - b["m_CharaID"])
      .sort((a, b) => a["sort_TitleID"] - b["sort_TitleID"]);

    this.charaList.forEach((chara) => {
      let id = "Img" + chara["m_CharaID"];
      this.initedImgList[id] = id;
    });

    // }

    if (this.storage.isSupported) {
      let ObjectiveChara_prom = this.storage.getItem("ObjectiveChara") || {};
      let checkerSettings_prom = this.storage.getItem("checkerSettings") || {};

      this.ObjectiveChara = (await ObjectiveChara_prom) || {};
      this.checkerSettings = (await checkerSettings_prom) || {};
    }
    this.isDownloading = false;

    this.isLoaded = true;
  },
};
</script>
